import axios from "axios";
import { useState } from "react";
import Header from "./components/Header";
import MovieList from "./components/MovieList";
import Search from "./components/Search";
import Loading from "./components/Loading"
import './css/app.custom.css';

const init = [  
    {
      "Title": "Iron Man",
      "Year": "2008",
      "imdbID": "tt0371746",
      "Type": "movie",
      "Poster": "https://m.media-amazon.com/images/M/MV5BMTczNTI2ODUwOF5BMl5BanBnXkFtZTcwMTU0NTIzMw@@._V1_SX300.jpg"
    },
    {
      "Title": "Iron Man 3",
      "Year": "2013",
      "imdbID": "tt1300854",
      "Type": "movie",
      "Poster": "https://m.media-amazon.com/images/M/MV5BMjE5MzcyNjk1M15BMl5BanBnXkFtZTcwMjQ4MjcxOQ@@._V1_SX300.jpg"
    },
    {
      "Title": "Iron Man 2",
      "Year": "2010",
      "imdbID": "tt1228705",
      "Type": "movie",
      "Poster": "https://m.media-amazon.com/images/M/MV5BMTM0MDgwNjMyMl5BMl5BanBnXkFtZTcwNTg3NzAzMw@@._V1_SX300.jpg"
    },
    {
      "Title": "Tetsuo: The Iron Man",
      "Year": "1989",
      "imdbID": "tt0096251",
      "Type": "movie",
      "Poster": "https://m.media-amazon.com/images/M/MV5BODYxZTIwMWQtZTdiMS00ODRmLThlODEtNjkwNmE1ZTY1ZjM1XkEyXkFqcGdeQXVyNzc5MjA3OA@@._V1_SX300.jpg"
    }
]
const App = () => {  
  const [search, setSearch] = useState(init);
  const [loading, setLoading] = useState(false);
  const [searchingValue, setSearchingValue] = useState("");
  const handlerSearchingValue = (e) => setSearchingValue(e.target.value);
  
  const getSearchingValue = () => {
    setLoading(true);
    axios.get(`https://www.omdbapi.com/?s=${searchingValue.split(' ').join('+')}&apikey=4a3b711b`)     
    .then( response => { 
      if (response.data.Response === "True") {
        setSearch(response.data.Search);        
      } else {
        alert(response.data.Error)
      } setLoading(false);
    });        
  }  

  return (       
    <div className='app'>      
      <Header />
      <Search searchingValue={searchingValue} handlerSearchingValue={handlerSearchingValue} getSearchingValue={getSearchingValue} />
      {loading ? <Loading /> : <MovieList movies={search} />}
    </div>
  );
}

export default App;
